<template>
  <div>
    <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box">
          <h4 class="subtitle">DAILY GRAPH DATA SMS</h4>
                  <h4 class="title">SMS GRAPH</h4>
                  <div class="block">
                    <chart :type="'line'" :data="loadData" :options="options"></chart>
                  </div>
        </article>
      </div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'
  import Chart from 'vue-bulma-chartjs'

  export default {
    components: {
      Chart
    },
    data () {
      return {
        fullHeight: document.documentElement.clientHeight,
        labels: ['Jan', 'Feb', 'Mar', 'Apr', 'Mei', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Des'],
        data: [
          [2, 4, 1, 2, 8, 2, 5],
          [35, 59, 30, 21, 56, 55, 40]
        ],
        options: {
          // segmentShowStroke: false,
          tooltips: {
            mode: 'label'
          }
        },
        backgroundColor: [
          'rgba(151, 155, 18, 1)',
          'rgba(31, 200, 219, 1)'
        ],
        series: ['Failed', 'Success']
      }
    },
    ready () {
      this.loadData()
    },
    methods: {
      today () {
        return moment().format('YYYY-MM-DD hh:mm:ss')
      },
      loadData () {
        this.$http({
          url: 'http://5.189.176.218:6680/graph2/daily',
          method: 'post',
          data: {
            startDate: moment().format('YYYY-MM-DD')
          }
        }).then((response) => {
          // console.log(response)
          this.series = response.data.series
          this.labels = response.data.labels
          this.data = response.data.data
          let data = {
            labels: response.data.labels
          }
          data.datasets = response.data.series.map((e, i) => {
            return {
              // data: list[i],
              data: response.data.data,
              // label: labels[i],
              label: response.data.labels[i],
              borderColor: this.backgroundColor[i].replace(/1\)$/, '.5)'),
              pointBackgroundColor: this.backgroundColor[i],
              backgroundColor: this.backgroundColor[i].replace(/1\)$/, '.5)')
            }
          })
          console.log(data)
          return data
        }).catch((error) => {
          console.log(error)
        })
      }
      // loadData () {
      //   let data = {
      //     labels: this.labels
      //   }
      //   data.datasets = this.series.map((e, i) => {
      //     return {
      //       // data: list[i],
      //       data: this.data[i],
      //       // label: labels[i],
      //       label: this.series[i],
      //       borderColor: this.backgroundColor[i].replace(/1\)$/, '.5)'),
      //       pointBackgroundColor: this.backgroundColor[i],
      //       backgroundColor: this.backgroundColor[i].replace(/1\)$/, '.5)')
      //     }
      //   })
      //   // console.log(data)
      //   return data
      // }
    }
  }
</script>

<style lang="scss">
.table-responsive {
  display: block;
  width: 100%;
  min-height: .01%;
  overflow-x: auto;
}
</style>



